<template>
  <div id="richList" :class="{hasHeader: $utils.showNavBar()}">
    <div class="header-title f34 colorFFF" v-if="$utils.showNavBar()" style="z-index:3">
      <div class="imgBack" @click="goback(-1)">
        <img class="back-icon" src="../../assets/img/richList/back-icon.png" alt="">
      </div>
      富豪榜
    </div>
    <div class="header-nav">
      <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/rich_list.png" alt="">
    </div>
    <div class="nav-list">
      <div class="list-head f30">
          <p>排名</p>
          <p>用户</p>
          <p>掘金宝</p>
          <p>可兑换</p>
      </div>
      <ul class="list-content">
        <li class="list-li  f28 flex" v-for="( item , index ) in sort_list" v-if="item.equivalence_current" :key="index" @click="openDetail(item)"  :class="{active2:index===0?'active2':''}" >
          <div class="list-detail" v-if="index===0">
            <span  v-if='commonState.login_bol'>{{item.sort}}</span>
            <div class="crown" v-else>
              <img src="../../assets/img/richList/crown1.png" alt="">
            </div>
          </div>
          <div class="list-detail" v-else-if="index===1">
            <div class="crown">
              <img v-if="commonState.login_bol" src="../../assets/img/richList/crown1.png" alt="">
              <img v-else src="../../assets/img/richList/crown2.png" alt="">
            </div>
          </div>
          <div class="list-detail" v-else-if="index===2">
            <div class="crown" v-if="commonState.login_bol">
              <img src="../../assets/img/richList/crown2.png" alt="">
            </div>
            <span v-else>{{item.sort}}</span>
          </div>
          <div class="list-detail" v-else>{{item.sort}}</div>
          <div class="user-detail flex">
            <div class="user-img">
              <img v-if="item.avatar" :src="item.avatar" alt="">
              <img v-else src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png" alt="">
            </div>
            <div >
              <p class="text-left ">{{item.nickname}}</p>
              <p class=" f24 text-left">邀请<span class="redColor">{{item.invite_num }}</span>人
              </p>
            </div>
          </div>
          <div class="list-detail mR10">{{$utils.thousandRead(item.vcoin)}}<span v-if="($utils.thousandRead(item.vcoin))"></span> </div>
          <div class="img-detail flex mR20">
            <div class="f20 shop-img" v-if="item.isSelf">
              <div class="selfImg" v-if="item.equivalence_current.price >= 9.9">
                <div class="carImage mB5"><img :src="item.equivalence_current.thumb" alt=""></div>
                <span>{{item.equivalence_current.shortname}}</span>
              </div>
              <div class="getGifts" @click="getGifts" v-else-if="item.equivalence_current && dataList.get_of_gift_bag === 0">
                <div class="iconSize">
                  <img src="../../assets/img/my/rich_big_bag.png" alt="大礼包图片">
                </div>
              </div>
              <div v-else class="gifts">
                <div class="iconSize">
                  <img src="../../assets/img/my/rich_bag.png" alt="红包">
                </div>
                <span class="ft24 nowrap">{{item.equivalence_current.price}}元</span>
              </div>
            </div>
            <div class="f20 shop-img" v-else>
              <div class="iconSize carImage mB5">
                <img :src="item.equivalence_current.thumb" alt="">
              </div>
              <div class="txt nowrap">{{item.equivalence_current.shortname}}</div>
            </div>
            <p class="rich-more mL10">
              <img v-show="item.equivalence_current.price * 1 >= 9.9" src="../../assets/img/richList/more-icon.png" alt="">
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div class="toInvite flex-row" @click="toInvite">
      <div class="flex-row button">我要上榜<div class="arrowEl"></div></div>
    </div>
  </div>
</template>

<script>
import counter from './counter.vue'
export default {
  data () {
    return {
      self: null,
      sort_list: null,
      dataList: {}
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    },
    share () {
      return this.$store.state.share
    }
  },
  mounted () {
    window.scrollTo(0, 0)
    if (this.$utils.cache('dataList')) {
      let data = JSON.parse(this.$utils.cache('dataList'))
      this.dataList = data
      this.self = data.self
      if (data.sort_list[0].isSelf && !this.commonState.login_bol) {
        data.sort_list.shift()
      } else if (!data.sort_list[0].isSelf && this.commonState.login_bol) {
        data.sort_list.unshift(Object.assign({isSelf: true}, data.self))
      }
      this.sort_list = data.sort_list
    } else {
      this.getList()
    }
  },
  components: {
    counter
  },
  methods: {
    goback (index) {
      this.$router.go(index)
    },
    toInvite () {
      if (!this.commonState.login_bol) {
        this.$store.commit('common_login_box', true)
      } else {
        this.$router.push('/invite_friend')
      }
    },
    // 获取大礼包
    getGifts () {
      if (!this.commonState.login_bol) {
        this.$store.commit('common_login_box', true)
      } else {
        this.$store.commit('setShowShare', true)
        this.$store.commit('set_shareType', 9)
      }
    },
    getList () {
      this.$api.get('WECHAT_LIST', {}, res => {
        if (res.data) {
          let dataList = res.data
          this.dataList = dataList
          this.self = dataList.self
          if (this.commonState.login_bol) {
            dataList.self.isSelf = true
            dataList.sort_list.unshift(dataList.self)
          }
          for (let v of dataList.sort_list) {
            if (v.nickname) {
              v.nickname = this.$utils.splitStr(v.nickname)
            }
          }
          this.sort_list = dataList.sort_list
          this.$utils.cache('dataList', JSON.stringify(res.data))
        }
      })
    },
    openDetail (item) { // 跳转商品详情
      if (item.equivalence_current) {
        let LoctSrc
        if (item.equivalence_current.tag === 'car') {
          LoctSrc = '/car_detail/'
        } else if (item.equivalence_current.tag === 'goods') {
          LoctSrc = '/goods_detail/'
        } else {
          return
        }
        this.$router.push(LoctSrc + item.equivalence_current.id)
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getList()
      }
    },
    'share.showSuccessShare' (flag) {
      if (!flag && this.share.shareType === 10 &&  this.commonState.userInfo.achieved_gift_bag === 1) {
        this.getList()
      }
    }
  }
}
</script>

<style lang="scss" type="text/css" scoped>
.list-content{
  padding-bottom: 160px;
}
.toInvite {
  display: flex;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 160px;
  bottom: 0;
  background: #FFFFFF;
  box-shadow: 0 -1px 0 0 #EEEEEE;
  .button {
    background-image: linear-gradient(0deg, #F4A04F 0%, #E3B033 100%);
    width: 590px;
    height: 96px;
    border-radius: 96px;
    color: #fff;
    font-size: 36px;
    .arrowEl {
      width: 15px;
      height: 15px;
      border-color: #fff;
    }
  }
}
.header-title {
  height: 88px;
  line-height: 88px;
  background-color: #070a2d;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
#richList{
  .hasHeader {
    padding-top: 88px;
  }
}
.header-nav {
  width: 750px;
  height: 595px;
  img {
    width: 100%;
    height: 100%;
  }
}
.mb5 {
  margin-bottom: 0;
}
.carImage {
  width: 133px;
  height: 100px;
  img {
    width: 100%!important;
    height: 100%;
  }
}
.nav-list {
  .list-head {
    display: flex;
    height: 90px;
    line-height: 90px;
    font-weight: bold;
    background-image: linear-gradient(0deg, #FFFFFF 0%, rgba(255,255,255,0.72) 100%);
    box-shadow: 0 1px 0 0 #E0E0E0;
    p {
      flex: 1;
      text-align: center;
    }
  }
  .iconSize{
    margin-right: 0;
  }
  .list-li {
    height: 160px;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 2px 0 0 #F5F5F5;
    &.active2{
       background: #fcfcfc;
       box-shadow: 0 1px 0 0 #E0E0E0;
     }
    .list-detail:first-child {
      width: 137px;
      box-sizing: border-box;
    }
    .list-detail.mR10{
      width: 90px;
      box-sizing: border-box;
      text-align: left;
    }
    .img-detail{
      max-width: 182px;
      justify-content: flex-end;
      overflow: hidden;
    }
    .user-detail {
      width: 260px;
      white-space: nowrap;
      box-sizing: border-box;
      overflow: hidden;
      display: -webkit-box;
      p{
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        margin-top: 10px;
      }
    }

    .list-detail,
    .user-detail,
    .img-detail {
      text-align: center;
      .user-img {
        width: 80px;
        height: 80px;
        margin-right:10px;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .shop-img {
        /*line-height: 30px;*/
        width: 180px;
        line-height: 1.5;
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-end;
        align-items: flex-end;
        .txt{
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 22px;
          text-align: center;
          width: 134px;
          max-width: 134px;
        }
        .iconSize {
          width: 133px;
          height: 100px;
          margin-right: 0;
          display: flex;
          justify-content: flex-end;
        }
       
        .gifts{
          width: 182px;
          margin-right: -30px;
          position: relative;
          justify-content: flex-end;
          span{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 4px;
            background: #ef3c31;
            color: #f7cb1f;
            padding: 0 30px;
            max-width: 135px;
            overflow: hidden;
            font-size: 22px;
            display: inline-block;
          }
          .iconSize {
            width: 178px!important;
            height: 100px;
            margin-right: -20px;
          }
        }

        .getGifts {
          height: 114px;
          width: 178px;
          margin-right: -25px;
          .iconSize {
            height: 114px!important;
            width: 178px!important;
            margin-right: -20px;
            // margin-left: -20px;
          }
        }
      }
      .rich-more {
        width: 19px;
        height: 32px;
        margin-top:40px;
        display: flex;
        align-items: center;
        img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
        }
      }
      .selfImg{
        display: flex;
        justify-content: flex-end;
        flex-flow:column wrap;
        align-items: flex-end;
        span {
          font-size: 22px;
          line-height: 1.5;
          display: inline-block;
          max-width: 133px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      .crown {
        width: 60px;
        height: 49px;
        margin: 0 auto;
        img {
          width: 100%;
        }
      }
    }
  }
}
.footer {
  background-color: #070a2d;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 130px;
  line-height: 130px;
  .rich-footer {
    text-align: center;
    background: rgba(5, 231, 255, 0.2);
  }
}
</style>



