<template>
  <div>{{countTime}}</div>
</template>

<script>
export default {
  data () {
    return {
      countTime: ''
    }
  },
  props: {
    endTime: ''
  },
  created () {
    this.formate()
  },
  watch: {
    countTime (newValue, oldValue) {
      if (newValue === '0天2:46:40') {
        this.$emit('beginTime', '')
      }
    }
  },
  methods: {
    formate () {
      this.countTime = this.endTime
      let time = Number(this.endTime)
      let d = Math.floor((time / 60 / 60) / 24)
      let h = Math.floor(time / 60 / 60 - d * 24)
      let m = Math.floor(time / 60 - d * 24 * 60 - h * 60)
      let s = Math.floor(time - d * 24 * 60 * 60 - h * 60 * 60 - m * 60)
      this.countTime = d + '天' + h + ':' + m + ':' + s
    }
  }
}
</script>

<style scoped>
</style>



