radius: 50%;\n        }\n      }\n      .shop-img {\n        /*line-height: 30px;*/\n        width: 180px;\n        line-height: 1.5;\n        display: flex;\n        flex-flow: column wrap;\n        justify-content: flex-end;\n        align-items: flex-end;\n        .txt{\n          white-space: nowrap;\n          overflow: hidden;\n          text-overflow: ellipsis;\n          font-size: 22px;\n          text-align: center;\n          width: 134px;\n          max-width: 134px;\n        }\n        .iconSize {\n          width: 133px;\n          height: 100px;\n          margin-right: 0;\n          display: flex;\n          justify-content: flex-end;\n        }\n       \n        .gifts{\n          width: 182px;\n          margin-right: -30px;\n          position: relative;\n          justify-content: flex-end;\n          span{\n            position: absolute;\n            left: 50%;\n            transform: translateX(-50%);\n            bottom: 4px;\n            background: #ef3c31;\n            color: #f7cb1f;\n            padding: 0 30px;\n            max-width: 135px;\n            overflow: hidden;\n            font-size: 22px;\n            display: inline-block;\n          }\n          .iconSize {\n            width: 178px!important;\n            height: 100px;\n            margin-right: -20px;\n          }\n        }\n\n        .getGifts {\n          height: 114px;\n          width: 178px;\n          margin-right: -25px;\n          .iconSize {\n            height: 114px!important;\n            width: 178px!important;\n            margin-right: -20px;\n            // margin-left: -20px;\n          }\n        }\n      }\n      .rich-more {\n        width: 19px;\n        height: 32px;\n        margin-top:40px;\n        display: flex;\n        align-items: center;\n        img {\n          width: 100%;\n          height: 100%;\n          vertical-align: middle;\n        }\n      }\n      .selfImg{\n        display: flex;\n        justify-content: flex-end;\n        flex-flow:column wrap;\n        align-items: flex-end;\n        span {\n          font-size: 22px;\n          line-height: 1.5;\n          display: inline-block;\n          max-width: 133px;\n          overflow: hidden;\n          text-overflow: ellipsis;\n          white-space: nowrap;\n        }\n      }\n      .crown {\n        width: 60px;\n        height: 49px;\n        margin: 0 auto;\n        img {\n          width: 100%;\n        }\n      }\n    }\n  }\n}\n.footer {\n  background-color: #070a2d;\n  position: fixed;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  width: 100%;\n  height: 130px;\n  line-height: 130px;\n  .rich-footer {\n    text-align: center;\n    background: rgba(5, 231, 255, 0.2);\n  }\n}\n",null],"sourceRoot":""}]);

